import { onBeforeUnmount } from 'vue'

export default function (listen_this, Website_Details) {
  const io = new IntersectionObserver(SeeElement, {
    root: null,
    rootMargin: '0px',
    threshold: 0.1,
  })
  io.observe(listen_this.value)

  function SeeElement(entries) {
    try {
      if (entries[0].isIntersecting) {
        Website_Details.value.classList.remove('element_float')
      } else {
        Website_Details.value.classList.add('element_float')
      }
    } catch (e) {
      e instanceof TypeError ? null : console.log(e)
    }
  }

  onBeforeUnmount(() => {
    io.disconnect()
  })
}
